const IntroductionMain = (props) => {
  return(
    <div className="container1">
      <div className="brief-intro" >
        <div className="Intro-Top" >
          <h2>संक्षिप्त परिचय</h2>
      <div className="intro-after"></div>
        </div>
        <div className="introduction-conetent" >
      <p className="mt">
        {message}

      </p>
      {/* <div className="IntrodctionTable" style={{width:"80%"}}>

      </div> */}

        </div>
  

    </div>
    <div className="team-wrapper">
      <div className="Intro-Top">
      <h2>पदाधिकारीहरु</h2>
    <div className="intro-after"></div>
      </div>
    <div className="our-team">
{
TeamData.map((value)=>{
return(<TeamCard 
image={value.image}
name={value.name}
post={value.post}
email={value.email}
emailseccond={value.emailseccond}
phone={value.phone}
/>)
})
}
</div>
</div>
</div>
   )

 }

export default IntroductionMain